<template>
  <v-app>
    <v-app-bar app
               color="deep-purple darken-3"
               dense
               elevation="10"
               outlined
               rounded
               class="white--text"
    >
      <v-app-bar-nav-icon class="white--text"></v-app-bar-nav-icon>
      <v-toolbar-title>EscapeGame</v-toolbar-title>
      </v-app-bar>
    <v-content>
      <v-container class="my-8">
        <v-layout row wrap>
          <v-flex xs12 md6 lg6 xl6 class="pa-4 ">
            <v-btn
                @click="ledOn"
                block
                color="red accent-2"
                elevation="3"
            >LED ON</v-btn>
          </v-flex>
          <v-flex  xs12 md6  lg6 xl6 class="pa-4">
            <v-btn
                @click="ledOff"
                block
                color="red accent-2"
                elevation="3"
            >LED OFF</v-btn>
          </v-flex>

        </v-layout>







      </v-container>

    </v-content>
  </v-app>
</template>

<script>
export default {
  name: "ControleLed",
  methods:{
    ledOn:function (){
      var json = {etat:true};
      fetch('http://10.0.0.21:8080/app/escapeGame/api/setEtatLed', {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(json)
      }).then(res => {
        return res.text()}).then(data =>{
        alert(data);
    })
  },
  ledOff: function ()
  {
    var json = {etat:false};
    fetch('http://10.0.0.21:8080/app/escapeGame/api/setEtatLed', {
      method: 'POST',
      headers: {'Content-Type': 'application/json'},
      body: JSON.stringify(json)
    }).then(res => {
      return res.text()}).then(data =>{
      alert(data);
    })
  }


}
}
</script>

